
.MuiMenu-paper {
  top: '0' !important;
}
.MuiPopover-paper {
  background-color: #FFF !important;
  border: none;
  box-shadow: none !important;
  border-top-left-radius: 0 !important;
  border-top-right-radius: 0 !important;
  border-bottom-right-radius: 6px !important;
  border-bottom-left-radius: 6px !important;
  ul {
    background-color: #FFF ;
    border: none;
    padding: 0 28px;
    margin: 0;
    li {
      margin: 0;
      padding-bottom: 6px;
      padding-top: 0;
      width: 100%;
      border-bottom: 1px solid rgba(32, 3, 46, 0.1);
      color: rgba(25,0,89,1);
      background-color: #FFF;
      justify-content: center;
      font-size: 1.25rem;
      font-weight: 700;
      line-height: 1.75rem;
      &:hover {
        color: rgb(173, 59, 249);
        background-color: #FFF !important;
      }
      &:last-of-type {
        border: none;
      }
    }
  }
}
.MuiListItem-root.Mui-focusVisible {
  background-color: #FFF !important;
}