.faq-row-wrapper {
    // margin: 0 150px 0 150px;

    .faq-title {
        justify-content: center;
        padding-bottom: 40px;
        font-family: "Manrope";
        font-weight: 700;
        font-size: 60px;
        font-style: normal;
        border-bottom: 1px solid #393953;
    }
    .faq-body {

        .faq-row {
            border-bottom: 1px solid #393953;

            .row-title {
                padding: 30px 0 30px 0;
                font-family: "Manrope";
                font-weight: 500;
                font-style: normal;
                font-size: 18px;

                .icon-wrapper {
                    position: absolute;
                    top: 30px;
                    font-size: 24px;
                }
            }
            .row-content {

                .row-content-text {
                    font-family: "Manrope";
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #C8C7D8;
                }
            }
        }
    }
}

@media (max-width: '900px') {
    .faq-row-wrapper {
        margin: 0 10%;

        .faq-title {
            font-size: 40px;
        }
    }
}
